import Vue from 'vue';
import ReInitPaymentButton from './components/ReInitPaymentButton';
import ReInitPaymentContainer from './components/ReInitPaymentContainer';

Vue.component('mollie-re-init-payment-btn', ReInitPaymentButton);
Vue.component('mollie-re-init-payment-container', ReInitPaymentContainer);


if (localStorage.getItem('mollieApplePayChecked') !== 'true') {

    if (window.ApplePaySession && window.ApplePaySession.canMakePayments()) {
        // Add ApplePay to the method selection in your checkout
        $.put('/rest/mollie/activate_apple_pay')
            .done(() => {
                console.log('apple pay active');
            })
            .fail((jqXHR) => {
            });
    }

    localStorage.setItem('mollieApplePayChecked', 'true');
}


